import { createElements } from "./createMarkup.js"
const list = document.querySelector(".list")
export const getFruits = () => {
    return fetch("http://localhost:1234/fruits").then((response) => {
        return response.json()
    }).then((data) => {
        console.log(data)
        list.insertAdjacentHTML("beforeend", createElements(data))
    }).catch((error) => {
        console.error(error)
    })
} 


const form = document.querySelector(".form")
const name = document.querySelector(".name")
const price = document.querySelector(".price")
const photo = document.querySelector(".photo")
const submit = document.querySelector(".submit")

// const params = new URLSearchParams({
//     _fruitname: ""
// })

export const addFruit = (fruitname, fruitprice, fruitphoto) => {
    const fruit = {
        name: fruitname,
        price: fruitprice,
        photo: fruitphoto
    }
    const options =  {
        method: "POST",
        body: json.stringify(fruit)
    }
    return fetch("http://localhost:1234/fruits", options)
}

submit.addEventListener("submit", (e) => {
    e.preventDefault()
    const fruitname = e.target.elements.fruitname;
    const fruitprice = e.target.elements.fruitprice;
    const fruitphoto = e.target.elements.fruitphoto;
    console.log(fruitname)
    addFruit(fruitname, fruitprice, fruitphoto)
    getFruits()
})